import ShortagesModel from '../models/shortages';

export default async function updateShortages(shortagesList) {
  const Shortages = ShortagesModel();
  await Promise.all(
    shortagesList.map(async (shortage) => {
      return Shortages.findByIdAndUpdate(
        { _id: shortage._id },
        shortage.update
      );
    })
  );

  return { message: 'Shortages updated successfully' };
}
